    }

    h4,
    input,
    button {
      text-align: center;
      flex-basis: 30%;
      margin: 0 auto;
      padding: 0;
    }
  }
}

.button {
  cursor: pointer;
  text-align: center;
  border: 5px solid $bgColor1;
  background-color: transparent;
  border-radius: 20px;
  color: $textColor1;
  position: relative;
  isolation: isolate;
  overflow: hidden;
  transition: color 1000ms, border-color 1000ms;
  &:focus-visible,
  &:hover {
    border-color: $bgColor1;
    outline: transparent;
    transition: color 1000ms, border-color 1000ms;
  }
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    background: $bgColor1;
    width: 10%;
    aspect-ratio: 1;
    border-radius: 50%;
    inset: 0;
    margin: auto;
    opacity: 0;
    transform: scale(1);
    transition: transform 1000ms, opacity 1000ms;
  }
  &:focus-visible::before,
  &:hover::before {
    transform: scale(20);
    opacity: 1;
    transition: transform 1000ms, opacity 500ms;
  }
}

.button1 {
  flex-basis: 100% !important;
  &::before {
    content: '';
    transition: transform 1500ms, opacity 2000ms;
  }
  &:focus-visible::before,
  &:hover::before {
    transition: transform 2000ms, opacity 1000ms;
  }
}

.Bookshelf {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
  padding: 0 50px;
  max-width: 1900px;
  margin: 0 auto;
  gap: 20px;
}

.Book {
  background-color: $bgColor1;
  display: flex;
  flex-direction: column;
  padding: 0 0 20px;
  border-radius: 20px;
  overflow: hidden;
  flex-basis: 95%;
  transform: scale(1);
  transition: transform 500ms;
  &:hover {
    transform: scale(1.2);
    z-index: 99;
    transition: transform 500ms;
  }
  img {
    width: 100%;
    margin: 0 auto;
    aspect-ratio: 16/9;
  }
  h4#title {
    color: $textColor1;
    font-size: $fs-400;
    text-align: center;
    line-height: 1.5em;
    letter-spacing: 0.2em;
    margin-top: 10px;
    padding: 0 10px;
    font-weight: 800;
  }
  h5 {
    color: $textColor2;
    font-size: $fs-400;
    padding: 0 10px;
    text-align: center;
    line-height: 1.5em;
    letter-spacing: 0.2em;
    margin-top: 10px;
    font-weight: 600;
  }
  h6 {
    color: $textColor2;
    font-size: $fs-200;
    text-align: center;
    margin-top: 10px;
    line-height: 1em;
    letter-spacing: 0.1em;
    font-weight: 400;
  }
}

@media screen and (min-width: 790px) {
  .Book {
    flex-basis: 45%;
  }
}

@media screen and (min-width: 1170px) {
  .Book {
    flex-basis: 30%;
  }
}

main.moviesMain {
